@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@300;400;500;700;800&display=swap);
:root{--c-pink: hsl(338, 60%, 65%);--c-pink-d: hsl(338, 60%, 55%);--c-white: whitesmoke;--border: 1px solid cyan;--boxshadow: 0px 7px 10px 0px rgba(0, 0, 0, 0.4);--heading-1: 7.594rem;--heading-2: 5.063rem;--heading-3: 3.375rem;--heading-4: 2.25rem;--f-normal: 1.6rem;--transition: all 300ms}.app-container--empty{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%)}*,*::before,*::after{padding:0;margin:0;box-sizing:inherit}html{font-size:62.5%;box-sizing:border-box}body{font-family:'Raleway', sans-serif;font-weight:500;font-size:100%;line-height:1.6;background-color:hsl(338, 60%, 65%);background-color:var(--c-pink)}img{display:inline-block}a{text-decoration:none}.heading{margin:1rem 0;font-weight:700}.heading-1{font-size:7.594rem;font-size:var(--heading-1)}.heading-2{font-size:5.063rem;font-size:var(--heading-2)}.heading-3{font-size:3.375rem;font-size:var(--heading-3)}.heading-4{font-size:2.25rem;font-size:var(--heading-4)}.d-flex{display:flex}.d-flex-ai-c{align-items:center}.m-0{margin:0}.app-container{background-color:whitesmoke;background-color:var(--c-white);max-width:45rem;min-width:45rem;margin:5rem auto;box-shadow:0px 7px 10px 0px rgba(0, 0, 0, 0.4);box-shadow:var(--boxshadow);border-radius:0.5rem;padding:2.5rem}.app-container--empty{margin:unset}.birthDay-preview{padding:1rem 0}.birthDay-preview__image{width:8rem;height:8rem;background-color:gray;border-radius:50%;object-fit:cover}.birthDay-preview__body{font-size:1.6rem;font-size:var(--f-normal);padding:0 1.5rem}.btn{border:none;outline:none;cursor:pointer;display:inline-block;transition:all 300ms;transition:var(--transition);padding:1rem;text-align:center}.btn--pink{background-color:hsl(338, 60%, 65%);background-color:var(--c-pink);color:whitesmoke;color:var(--c-white)}.btn--pink:hover{background-color:hsl(338, 60%, 55%);background-color:var(--c-pink-d)}.btn--block{width:100%}

